*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html{
    font-size: 62.5%;
}
body{
    height: 100vh;
}
.row{
    margin: 0;
}
.top_nav{
    background-color: #bfc1c8;
}
.navbar{
    color: #fff;
    background-color: #bfc1c8 ;
    padding: 2rem 0;
}

.navbar a{
    color: #fff;
    font-size: 1.8rem;
}
.navbar a:hover{
    color: #ec1839;
}
.navbar .navbar-brand {
    font-size: 3rem;
    color: goldenrod;
    font-weight: 700;
}

.navbar li{
    margin-left: 2rem;
}
.navbar .active a{
    color: #ec1839;
    font-weight: 700;
}
.navbar .lang{
    background-color: gray;
    padding: 10px 10px;
    border-radius: 30px;
}
.navbar .dropdown-item{
    color: black;
}
.fa-bell{
    font-size: 3rem;
}
.serach_form{
    width: 50%;
    position: relative;
    margin: auto;
}
.serach_form input[type="text"]{
    width: 100%;
    color: gray;
    outline: none;
    border: none;
    border-radius: 20px;
    padding: 20px 30px;
    font-size: 1.4rem;
    
}
.serach_form input[type="submit"]{
    position: absolute;
    top: 0;
    right: 0;
    border: none;
    outline: none;
    color: white;
    background-color: #ec1839;
    padding: 10px 20px;
    border-radius: 20px;
    font-size: 1.4rem;
    box-shadow: 0 0 5px 2px rgb(185, 92, 170);
    
}
.serach_form input[type="submit"]:hover{
    box-shadow: 0 0 8px 3px rgb(185, 92, 170);
}
.navbar .fa-search{
    position: absolute;
    top: 13px;
    left: 8px;
    color: gray;
    font-size: 1.5rem;
}

.navbar-toggler{
    background-color: gray;
}



/* main menu */
.main_menu ul{
    margin-top: 30px;
    margin-bottom: 30px;
}
.main_menu ul li{
    display: inline-block;
    font-size: 1.6rem;
   

}
.main_menu ul li a{
    text-decoration: none;
    padding: 10px 10px;
    color: gray;
    border-radius: 20px;
}


.main_menu ul li a:hover{
    background-color: #ec1839;
    color: white;
}

.content_place .row .content{
    background-color: #bfc1c8;
    height: 300px;
    border-radius: 20px;
    

}
.main_menu ul li .active{
    background-color: #ec1839;
    color: white;
}
.main_menu ul li .more{
    color: #ec1839 ;
    font-weight: 700;
}


@media(max-width:995px){
    .serach_form{
        width: 80%;
    }
    .navbar .lang{
        background-color: #bfc1c8;
    }
.fa-bell{
    display: none;
}
.hidden{
    display: none;
}
}

@media(max-width:667px){
    .serach_form{
        width: 80%;
    }
    .navbar .lang{
        background-color: #bfc1c8;
    }
    .hide{
        display: none;
    }
    .main_menu li a{
        font-size: 1.3rem;
        font-weight: 600;
    }
}